<template lang="html">
  <div class="address-list borderB"><!-- @click="$router.push('/cardsearchaddress')"-->
    <i class="del-icon" v-show="isShow" @click.stop="$emit('delete', index)"></i>
    <i class="position__icon"></i>
    <p class="address-list__detail">{{address}}</p>
  </div>
</template>

<script>
export default {
  props: {
    isShow: {
      type: Boolean,
      defaule: false
    },
    address: {
      type: String,
      defaule: ''
    },
    index: {
      type: Number
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';

.address-list {
  font-size: .32rem;
  position: relative;
  padding: .16rem 0 .06rem 0;
  .address-list__detail {
    color: @midleColor;
    padding-left: .6rem;
    line-height: .56rem;
  }
  p {
    height: 1rem;
  }
  .position__icon {
    right: 0;
    bottom: .1rem;
  }
  .del-icon {
    position: absolute;
    width: .4rem;
    height: .4rem;
    left: 0;
    top: .48rem;
    background: url(../../assets/img/del@2x.png) no-repeat;
    background-size: .4rem .4rem;
  }
}
</style>



