<template lang="html">
  <div class="add-address-page">
    <!--  -->
    <default-head header="" :path="path">
      <span slot="head-right" @click="changeStates">{{delToggle}}</span>
    </default-head>
    <white-title text="" :isShow="true"
      @click.native="$router.push({ path: '/cardaddaddress', query: { sign: sign } })"></white-title>
    <light-title v-show="addressDate.length > 0" text=""></light-title>
    <!--  -->
    <div class="address-list__box">
      <div class="address-data" >
        <address-list :isShow="isShow" v-for="(item, index) in addressDate"
        :address="item.address" :key="index" :index="index" @click.native="selectAddress(item)" @delete="delItem"></address-list>
      </div>
    </div>
    <foot-btn v-show="isShow" @click.native="submitDel"></foot-btn>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import lightTitle from '@/components/common/lightTitle'
import whiteTitle from '@/components/common/whiteTitle'
import addressList from '@/components/common/addressList'
import footBtn from '@/components/common/footBtn'

export default {
  components: { defaultHead, lightTitle, whiteTitle, addressList, footBtn },
  data () {
    return {
      isShow: false,
      delToggle: '',
      addressDate: [],
      delAddress: [],
      sign: ''
    }
  },
  computed: {
    userId () {
      return utils.userId()
    },
    path () {
      let sign = this.$route.query.sign
      return sign === 'company' ? '/companyinfo' : '/personinfo'
    }
  },
  created () {
    this.initPage()
  },
  methods: {
    isLogin () {
      if (!utils.isLogin()) {
        this.$router.push('/login')
        return false
      }
      return true
    },
    initPage () {
      if (!this.isLogin()) return
      this.sign = this.$route.query.sign
      this.ajaxCommonAddess({userId: this.userId, type: this.sign === 'company' ? 1 : 0})
    },
    changeStates () {
      this.isShow = !this.isShow
      !this.isShow ? this.delToggle = '' : this.delToggle = ''
    },
    delItem (index) {
      this.delAddress.push(this.addressDate[index].id)
      this.addressDate.splice(index, 1)
    },
    submitDel () {
      if (!this.isLogin()) return
      if (this.delAddress.length <= 0) {
        minutils.MessageBox({message: ''})
        return
      }
      this.ajaxDeleteAddress({ids: this.delAddress.join()})
    },
    selectAddress (item) {
      // 
      let data = JSON.stringify(item)
      if (this.sign === 'company') {
        sessionStorage.setItem('companyPosition', data)
        this.$router.push('/companyinfo')
      } else {
        sessionStorage.setItem('personPosition', data)
        this.$router.push('/personinfo')
      }
      let json = {...item, isDefault: 1, userId: this.userId}
      this.ajaxSaveAddress(json)
    },
    ajaxSaveAddress (data) {
      api.saveAddress(data).then(response => {
        let result = response.body
        if (result.isSuccess && result.data) {
        }
      })
    },
    ajaxCommonAddess (data) {
      minutils.IndicatorOpen('...')
      api.getCommonAddress(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess) {
          this.addressDate = result.data
        }
      })
    },
    ajaxDeleteAddress (data) {
      minutils.IndicatorOpen('...')
      api.deleteAddress(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess) {
          minutils.MessageBox({message: ''})
        }
      })
    }
  }
}
</script>

<style lang="less">
@import '../assets/css/variable.less';
.address-list__box {
  margin-bottom: 1rem;
}
.add-address-page {
  padding-top: .88rem;
  .head-right {
    color: @activeColor;
  }
  .address-data {
    padding: 0 .22rem;
    background: @whiteColor;
  }
}
</style>



